.message {
    margin-bottom: 47px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    gap: 30px;
}

.imageAndText img {
    width: 48px;
    height: 48px;
}

.text {
    padding: 8px 13px;
    background-color: #06C;
    border-radius: 13px;
    text-align: end;
}

.text:after {

}

.name {
    color: #FFF;
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;

    margin-bottom: 3px;
}

.messageText {
    color: #FFF;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.time {
    color: #000;
    font-family: Montserrat;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;

    margin-right: 10px;
}